
 export const  RegForm = (lang,nameUser,email,message,setValidName,setValidEmail,setCheckEmail,setValidMessege)=> {

    function regName() {
        let re = /[^a-zA-Z0-9]+/g;
        if (nameUser.length < 3 || re.test(String(nameUser))) {
            setValidName(lang === "ru" ? `Правильно напишите свое имя,должно быть больше чем 2 символа,только буквы и цифры` : `Write please correct your name must be more than 2 letters,only letters and number`);
        } else {
            setValidName('')

        }
    }

     function regEmail() {
        const re2 = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (!re2.test(String(email).toLowerCase())) {
            setValidEmail(lang === "ru" ? `Правильно напишите ваш email` : `Please write correct your email`)
            setCheckEmail(false);
        } else {
            setValidEmail('')
            setCheckEmail(true);
        }
    }

     function regMessege() {
        if (message.length < 15) {
            setValidMessege(lang === "ru" ? `Правильно напишите ваше сообщение, должно быть больше чем 15 символов` : `Write please correct your message must be more than 15 letters`)
        } else {
            setValidMessege('')
        }
    }
    regName();
    regMessege();
    regEmail();
}
